@import url(https://fonts.googleapis.com/css?family=Open+Sans:400italic,600italic,700italic,800italic,400,600,700,800);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background:grey;width:100%;height:100vh;padding:120px 0;font-family:'Open Sans', sans-serif;box-sizing:border-box;display:flex;align-items:center;justify-content:center}img{max-width:100%}.profile-card{position:relative;width:280px;margin:0 auto;padding:40px 30px 30px;background:#fff;border:5px solid rgba(255,255,255,0.7);text-align:center;border-radius:4px;transition:all 200ms ease}.mask-shadow{z-index:-1 !important;width:95%;height:12px;background:#000;bottom:0;left:0;right:0;margin:0 auto;position:absolute;border-radius:4px;opacity:0;transition:all 400ms ease-in}.profile-card:hover{box-shadow:0px 30px 60px -5px rgba(55,55,71,0.3);transform:translate3d(0, -5px, 0)}.profile-card:hover .mask-shadow{opacity:1;box-shadow:0px 30px 60px -5px rgba(55,55,71,0.3);position:absolute}.profile-card header{display:block;margin-bottom:10px}.profile-card header a{width:150px;height:150px;display:block;border-radius:100%;margin:-120px auto 0;box-shadow:0 0 0 5px #82b541}.profile-card header a img{border-radius:50%;width:150px;height:150px}.profile-card:hover header a,.profile-card header a:hover{animation:bounceOut .4s linear;-webkit-animation:bounceOut .4s linear}.profile-card header h1{font-size:20px;color:#444;text-transform:uppercase;margin-bottom:5px}.profile-card header h2{font-size:14px;color:#acacac;text-transform:uppercase;margin:0}.profile-bio{font-size:14px;color:#a5a5a5;line-height:1.7;font-style:italic;margin-bottom:15px}.profile-social-links{margin:0;padding:0;list-style:none;display:flex;align-items:center;justify-content:center}.profile-social-links li{display:inline-block;margin:0 10px}.profile-social-links li a{width:55px;height:55px;display:flex;background:#f1f1f1;justify-content:center;align-items:center;border-radius:50%;transition:all 2.75s cubic-bezier(0, 0.83, 0.17, 1);transform-style:preserve-3d;text-decoration:none}.profile-social-links li a img{width:35px;height:35px}.profile-social-links li a:hover{background:#ddd;transform:scale(1.2);-webkit-transform:scale(1.2)}@-webkit-keyframes bounceOut{0%{box-shadow:0 0 0 4px #82b541;opacity:1}25%{box-shadow:0 0 0 1px #82b541;opacity:1}50%{box-shadow:0 0 0 7px #82b541;opacity:1}75%{box-shadow:0 0 0 4px #82b541;opacity:1}100%{box-shadow:0 0 0 5px #82b541;opacity:1}}@keyframes bounceOut{0%{box-shadow:0 0 0 6px #82b541;opacity:1}25%{box-shadow:0 0 0 2px #82b541;opacity:1}50%{box-shadow:0 0 0 9px #82b541;opacity:1}75%{box-shadow:0 0 0 3px #82b541;opacity:1}100%{box-shadow:0 0 0 5px #82b541;opacity:1}}.company{text-transform:none !important;text-decoration:underline;cursor:pointer}.social-icon{font-size:25px}.hobbies{margin-top:10px !important}.button-chat{margin:10px 0}

